import { Component, OnInit, OnDestroy } from '@angular/core';
import { BaseComponent } from 'src/app/modules/common/components/base/base.component';
import { Payload, BackButton } from 'src/app/modules/common/components/base/base';
import { ViewEncapsulation } from 'src/app/modules/common/components/encaptulation';
import { PAIS, MODULO, LENGUAJE, LOADING } from 'src/app/modules/common/CONST';
import { SafeResourceUrl } from '@angular/platform-browser';

@Component({
  selector: 'app-bio003',
  templateUrl: './bio003.component.html',
  styleUrls: ['./bio003.component.scss', '../../../../../assets/cam/scss/components/biometria/_bio.scss'],
  encapsulation: ViewEncapsulation.ShadowDom
})
export class Bio003Component extends BaseComponent implements OnInit, OnDestroy, Payload, BackButton {
  frontDni: SafeResourceUrl;
  backDni: SafeResourceUrl;
  intentosMaximos: any;

  ngOnInit(): void {
    this.getIntentosMaximos();
    if (this.canal === '1') {
      this.frontDni = this.sanitizer.bypassSecurityTrustUrl(this.imageFront) || '';
      this.backDni = this.sanitizer.bypassSecurityTrustUrl(this.imageBack) || '';
    } else {
      this.obser.sendData({
        mostrar: true,
        footer: '<strong>Estamos procesando su solicitud</strong>',
        header: '<strong>Espere un momento por favor</strong>'
      }, LOADING);

      setTimeout(() => {
        this.onCall(this.getDataToPayload({
          // tslint:disable-next-line: max-line-length
          imagenReversoIdentificacion: this.imageBack ? this.imageBack : `60-520b-11ea-a9ac-1bda184621120-1582002106936|75abe960-520b-11ea-a9ac-1bda184621121-1582002106944|75abe960-520b-11ea-a9ac-1bda184621122-1582002106946|75abe960-520b-11ea-a9ac-1bda184621123-1582002106948|75abe960-520b-11ea-a9ac-1bda184621124-1582002106949`,
          // tslint:disable-next-line: max-line-length
          imagenFrenteIdentificacion: this.imageFront ? this.imageFront : `60-520b-11ea-a9ac-1bda184621120-1582002106936|75abe960-520b-11ea-a9ac-1bda184621121-1582002106944|75abe960-520b-11ea-a9ac-1bda184621122-1582002106946|75abe960-520b-11ea-a9ac-1bda184621123-1582002106948|75abe960-520b-11ea-a9ac-1bda184621124-1582002106949`,
          captureAgain: false
        }));
      }, 1000);
    }

  }

  ngOnDestroy(): void {
    // throw new Error('Method not implemented.');
  }

  getDataToPayload(data?: any): () => ({ [key: string]: any; }) {
    this.disableButton = true;
    return () => ({
      volverACapturar: data.captureAgain,
      maximosReintentosDocumento: this.intentosMaximos ? this.intentosMaximos : 3,
      imagenFrenteIdentificacion: data.imagenFrenteIdentificacion,
      imagenReversoIdentificacion: data.imagenReversoIdentificacion
    });
  }

  getDataToBackButton(data?: any): () => ({ [key: string]: any; }) {
    // throw new Error('Method not implemented.');
    return () => ({});
  }

  getIntentosMaximos() {
    const SUBSCRIPTION = this.catalogo.data('BIO001', this.params([PAIS, MODULO, LENGUAJE])).subscribe(
      response => {
        this.intentosMaximos = response && (response.length > 0)
        ? response[0].maximosReintentos.maximosReintentosDocumento : 1;
        setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
      }
    );
  }

}
