import { Component, OnInit, OnDestroy } from '@angular/core';
import { BaseComponent } from 'src/app/modules/common/components/base/base.component';
import { Payload, BackButton } from 'src/app/modules/common/components/base/base';
import { ViewEncapsulation } from 'src/app/modules/common/components/encaptulation';
import { ICaptureBackDocument, IBioConfig } from '../../common/models/biometria.model';
import { WebcamImage, WebcamInitError } from 'ngx-webcam';
import { Subject, Observable } from 'rxjs';
import { WEBCAM_ERRORS, MODAL, LOGGER, PAIS, IMAGE_BACK, LOADING, STEP_ID } from 'src/app/modules/common/CONST';
import { Modal } from 'src/app/modules/common/components/modal/modal';
import { AlertModalTMAComponent } from 'src/app/modules/common/components/entryComponents/alert-modal-tma/alert-modal-tma.component';


@Component({
  selector: 'app-bio002',
  templateUrl: './bio002.component.html',
  styleUrls: ['./bio002.component.scss', '../../../../../assets/cam/scss/components/biometria/_bio.scss'],
  encapsulation: ViewEncapsulation.ShadowDom
})
export class Bio002Component extends BaseComponent implements OnInit, OnDestroy, Payload, BackButton {
  backDni: string;
  trigger: Subject<void>;
  webcamErrors: Array<string>;
  mediaConstraints: any;
  widthImage: number;
  heightImage: number;
  imagePreview: string;
  captureFlag: boolean;

  urlSafe: any;

  private config: IBioConfig;
  ngOnInit(): void {
    this.webcamErrors = [];
    this.trigger = new Subject<void>();
    this.urlSafe = require('../../../../../assets/cam/img/bio/cedula_2.svg');
    this.imagePreview = '';
    this.captureFlag = true;
    // Determina el ancho y el alto de la imgen a capturar
    if (!this.isDesktopDevice) {
      this.widthImage = 360;
      this.heightImage = 360;
    } else {
      this.widthImage = 960;
      this.heightImage = 480;
    }
    this.getConfig();
    this.obser.getData(MODAL).subscribe((response) => {
      if (response ) {
        this.backDni = null;
        console.log('modal response:', response);
      }
    });

    if (this.canal === '2') {
      this.serverEnrollmentKey = this.payload.llaveEnrolamiento;
    }

  }

  ngOnDestroy(): void {
    // throw new Error('Method not implemented.');
  }

  getDataToPayload(data?: any): () => ({ [key: string]: any; }) {
    return () => ({
      imagenReversoIdentificacion: data.backDni || ''
    });
  }

  getDataToBackButton(data?: any): () => ({ [key: string]: any; }) {
    // throw new Error('Method not implemented.');
    return () => ({});
  }

  /*
    Se llama post message 'captureBackDocument' que activa la cmara
    del telfono para tomar la foto de la parte posterior del documento.
  */
  mobileCaptureBackDocument(): void {
    this.disableButton = true;
    const SUBSCRIPTION = this.captureBackDocument().subscribe((response: ICaptureBackDocument) => {
      if (response && response.status) {
        if (response.status === '01') {
          this.obser.sendData(false, LOADING);
          this.showModal(`Ha ocurrido un error, por favor vuelva a intentarlo`);
          this.disableButton = false;
          setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
          return false;
        }
        if (response.status === '02') {
          this.obser.sendData(false, LOADING);
          this.disableButton = false;
          setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
          return false;
        }
        if (response.status === '00') {
          this.obser.sendData({
            mostrar: true,
            footer: '<strong>Estamos procesando su solicitud</strong>',
            header: '<strong>Espere un momento por favor</strong>'
          }, LOADING);
          const obj: any = {};
          let cont = 0;
          const imgStrZip = this.config.compress === 'ACTIVE' ? this.zipString.compress(response.backDni) : response.backDni;
          const limite = +this.config.limit;
          for (let j = 0; j < imgStrZip.length; j = j + limite) {
            obj[`backDni_${cont}`] = imgStrZip.substr(j, limite);
            cont ++;
          }
          this.saveBackDocument(obj, false);
        }
        setTimeout(() => SUBSCRIPTION.unsubscribe(), 500);
      }
    });
  }

  /*
    Manda activar observable que captura la imagen de la camara web.
  */
  triggerCaptureBackDocument(): void {
    if (this.webcamErrors.length <= 0) {
      this.trigger.next();
    }
  }

  /*
    Evento que se activa cuando se captura la imagen de la camara web.
  */
  handleCaptureBackDocument(backDni: WebcamImage): void {
    if (backDni && this.captureFlag) {
      this.captureFlag = false;
      this.disableButton = true;
      if (backDni.imageAsDataUrl.toString().length > this.config.limit) {
        const SUBSCRIPTION = this.compress(backDni.imageAsDataUrl, this.config.web.width, this.config.web.quality).subscribe(backWebDni => {
          // this.urlSafe = this.sanitizer.bypassSecurityTrustUrl(backWebDni);
          this.imagePreview = backWebDni;
          this.obser.sendData(this.imagePreview, IMAGE_BACK);
          this.saveBackDocument({ backDni: backWebDni }, true);
          setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
        },
        error => {
          this.disableButton = false;
          this.captureFlag = true;
        });
      } else {
        this.imagePreview = backDni.imageAsDataUrl.toString();
        this.obser.sendData(this.imagePreview, IMAGE_BACK);
        this.saveBackDocument({ backDni: this.imagePreview }, true);
      }
    }
  }

  get triggerCaptureBackDocumentObservable(): Observable<void> {
    return this.trigger.asObservable();
  }

  handleInitError(error: WebcamInitError): void {
    if (error.mediaStreamError && WEBCAM_ERRORS.hasOwnProperty(error.mediaStreamError.name)) {
      // this.disableButton = true;
      this.webcamErrors.push(WEBCAM_ERRORS[error.mediaStreamError.name]);
    }
  }

  // Nueva funcin que captura la imgen
  capture(event: any) {
    this.disableButton = true;
    const reader = new FileReader();
    reader.readAsDataURL( event.target.files[0]);
    reader.onloadend = (ev: any) => {
      this.backDni = reader.result.toString();
      this.imagePreview = this.backDni;
      this.urlSafe = this.sanitizer.bypassSecurityTrustUrl(reader.result.toString());
      this.obser.sendData(this.imagePreview, IMAGE_BACK);
      if (reader.result.toString().length > this.config.limit) {
        const SUBSCRIPTION = this.compress(reader.result, this.config.mobile.width, this.config.mobile.quality).subscribe(backDni => {
          if (backDni.length > this.config.limit) {
            const SUBSCRIPTION2 = this.compress(reader.result, this.config.mobile.width * 0.9, this.config.mobile.quality - 0.1)
            .subscribe(backDni2 => {
              if (backDni2.length > this.config.limit) {
                const SUBSCRIPTION3 = this.compress(reader.result, this.config.mobile.width * 0.9, this.config.mobile.quality - 0.1)
                .subscribe(backDni3 => {
                  this.saveBackDocument({ backDni: backDni3 }, true);
                  setTimeout(() => SUBSCRIPTION3.unsubscribe(), 300);
                },
                error => {
                });
              } else {
                this.saveBackDocument({ backDni: backDni2 }, true);
              }
              setTimeout(() => SUBSCRIPTION2.unsubscribe(), 300);
            },
            error => {
              this.disableButton = true;
            });
          } else {
            this.saveBackDocument({ backDni }, true);
          }
          setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
        },
        error => {
          this.disableButton = false;
        });
      } else {
        this.saveBackDocument({ backDni: reader.result.toString() }, true);
      }
    };
  }

  saveBackDocument(images: { [key: string]: any }, encript: boolean) {
    this.disableButton = true;
    const SUBSCRIPTIONBACK = this.saveImages({ images }, encript).subscribe((response) => {
      if (response.data && response.data.status ) {
        const ref = response.data.imagesRef;
        let imgStr = '';
        Object.keys(ref).map((data, index) => {
          imgStr = index === 0 ? ref[data] : `${imgStr}|${ref[data]}`;
        });
        if (this.canal === '1') {
          this.onCall(this.getDataToPayload({ backDni: imgStr }));
          this.captureFlag = true;
        } else {
          this.obser.sendData(imgStr, IMAGE_BACK);
          this.obser.sendData(false, LOADING);
          this.obser.sendData('BIO003', STEP_ID);
          this.route.navigate(['cam/biometria/bio003']);
        }
        setTimeout(() => SUBSCRIPTIONBACK.unsubscribe(), 500);
      }
    },
    error => {
      this.disableButton = false;
      this.imagePreview = '';
      this.captureFlag = true;
      this.obser.sendData(false, LOADING);
      this.showModal(`Ha ocurrido un error, por favor vuelva a intentarlo`);

    });
  }

  getImageMimeType(imageBase64: string) {
    return imageBase64.match(/data:([a-zA-Z0-9]+\/[a-zA-Z0-9-.+]+).*,.*/)[1];
  }

  compress(file: any,  max: number, quality: number = 0.8): Observable<any> {
    const width = max;
    const reader = new Image();
    const mimeType = this.getImageMimeType(file);
    reader.src = file;
    return new Observable(observer => {
      reader.onload = ev => {
        const img = new Image();
        img.src = (ev.target as any).src;
        img.onload = () => {
          const elem = document.createElement('canvas');
          const scaleFactor = width / img.width;
          elem.width = width;
          elem.height = img.height * scaleFactor;
          const ctx = (elem.getContext('2d') as CanvasRenderingContext2D);
          ctx.drawImage(img, 0, 0, width, img.height * scaleFactor);
          const newBase64 = ctx.canvas.toDataURL(mimeType, quality);
          observer.next(newBase64);
        };
        reader.onerror = error => observer.error(error);
      };
    });
  }

  showModal(mensaje) {
    const modal: Modal = {
      display: true,
      buttons: [
        {
          callback: ( close ) => {
            close();
          },
          buttonText: 'Volver a tomar foto',
          class: 'alertModal__footer--button alertModal__footer--button--primary'
        }
      ],
      title: '',
      message: mensaje,
      entryComponent: AlertModalTMAComponent
    };
    this.obser.sendData(modal, MODAL);
  }

  // Consulta de catalogo la configuracin de la calidad de imagenes
  getConfig() {
    const SUBSCRIPTION = this.catalogo.data('BIOCONFIG', this.params([PAIS])).subscribe(
      response => {
        console.log(response);
        this.config = response && (response.length > 0)
        ? response[0] : {
          showLog: '0',
          limit: 900000,
          app: {
            quality: 0.7,
            width: 2400
          },
          mobile: {
            quality: 0.7,
            width: 2400
          },
          web: {
            quality: 0.8,
            width: 2400
          },
          compress: 'INACTIVE'
        };
        setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
      }
    );
  }

}
