import { Component, OnInit, OnDestroy } from '@angular/core';
import { BaseComponent } from 'src/app/modules/common/components/base/base.component';
import { Payload, BackButton } from 'src/app/modules/common/components/base/base';
import { ViewEncapsulation } from 'src/app/modules/common/components/encaptulation';
import { ICaptureFace, IBioConfig } from '../../common/models/biometria.model';
import { ZoomSDK, ZoomManagedSession } from '@ZoomSDK/ZoomAuthentication';
import { ZoomCustomization, ZoomOverlayCustomization } from '@ZoomSDK/ZoomCustomization';
import { PAIS, MODULO, LENGUAJE, FACE_IMAGE, LOGGER, MODAL, LOADING } from 'src/app/modules/common/CONST';
import { SafeResourceUrl } from '@angular/platform-browser';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { Modal } from 'src/app/modules/common/components/modal/modal';
import { AlertModalTMAComponent } from 'src/app/modules/common/components/entryComponents/alert-modal-tma/alert-modal-tma.component';

@Component({
  selector: 'app-bio004',
  templateUrl: './bio004.component.html',
  styleUrls: ['./bio004.component.scss', '../../../../../assets/cam/scss/components/biometria/_bio.scss'],
  encapsulation: ViewEncapsulation.ShadowDom
})
export class Bio004Component extends BaseComponent implements OnInit, OnDestroy, Payload, BackButton {
  face: SafeResourceUrl;
  faceExt: string;
  deviceLicenseKeyIdentifier: string;
  zoomServerBaseURL: string;
  zoomCustomization: ZoomCustomization;
  overlayCustomization: ZoomOverlayCustomization;
  zoomManagedSession: ZoomManagedSession;
  sessionId: string;
  intentosMaximos: any; // Valor por default

  private config: IBioConfig;
  ngOnInit(): void {
    this.face = require('../../../../../assets/cam/img/bio/foto.svg');
    if (this.canal === '1') {
      this.initializeZoomSDK();
    } else {
      this.serverEnrollmentKey = this.payload.llaveEnrolamiento;
    }
    this.getConfig();
    this.getIntentosMaximos();
  }

  ngOnDestroy(): void {
    // throw new Error('Method not implemented.');
  }

  getDataToPayload(data?: ICaptureFace): () => ({ [key: string]: any; }) {
    return () => ({
      mapaTridimensionalRostro: data.face || '',
      faceMap: data.faceMap,
      formatoImagenFacial: this.faceExt || '',
      maximosReintentosSelfie: this.intentosMaximos ? this.intentosMaximos : 3,
      idSesion: this.sessionId
    });
  }

  getDataToBackButton(data?: any): () => ({ [key: string]: any; }) {
    // throw new Error('Method not implemented.');
    return () => ({});
  }

  /*
    Se llama post message 'captureFace' que activa la cmara
    del telfono para tomar foto del rostro.
  */
  mobileCaptureFace() {
    const SUBSCRIPTION = this.captureFace().subscribe((response: ICaptureFace) => {

      if (response && this.hasProperty(response, 'status')) {
        if (response.status === '01') {
          this.obser.sendData(false, LOADING);
          this.showModal(`Ha ocurrido un error, por favor vuelva a intentarlo`);
          this.disableButton = false;
          setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
          return false;
        }
        if (response.status === '02') {
          this.obser.sendData(false, LOADING);
          this.disableButton = false;
          setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
          return false;
        }
        if (response.status === '00') {
          const urlTemp = response.face.trim().substr(0, 10) !== 'data:image' ?
          `data:image/jpeg;base64,${response.face}` :
          response.face;

          this.faceExt = this.getImageExtension(urlTemp);
          this.obser.sendData(urlTemp, FACE_IMAGE);
          this.face = this.sanitizer.bypassSecurityTrustUrl(urlTemp);
          this.sessionId = response.uploadMatchFaceRequest.additionalsParams[0].value ?
                          response.uploadMatchFaceRequest.additionalsParams[0].value :
                          '';
          if (urlTemp.length > this.config.limit) {
            const faceM = response.faceMap;
            const SUBSCRIPTION2 = this.compress(urlTemp, this.config.app.width, this.config.app.quality).subscribe( face2 => {
              this.saveFace({ face: face2, faceMap: faceM });
              setTimeout(() => SUBSCRIPTION2.unsubscribe(), 300);
            });
          } else {
            this.saveFace({ face: urlTemp, faceMap: response.faceMap });
          }
        }
        setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
      }
    });
  }

  /* Zoom SDK */
  initializeZoomSDK() {
    this.disableButton = true;
    this.deviceLicenseKeyIdentifier = environment.zoomDeviceLicenseKey;
    this.zoomServerBaseURL = environment.zoomServerBaseURL;
    ZoomSDK.setImagesDirectory('assets/cam/js/core-sdk/zoom_images');
    ZoomSDK.setResourceDirectory('assets/cam/js/core-sdk/ZoomAuthentication.js/resources');
    this.zoomCustomization = new ZoomSDK.ZoomCustomization();
    this.overlayCustomization = new ZoomSDK.ZoomOverlayCustomization();

    /* Customizacin */
    this.overlayCustomization.backgroundColor = 'rgba(119, 96, 77, 0.8)';
    this.zoomCustomization.overlayCustomization = this.overlayCustomization;
    ZoomSDK.setCustomization(this.zoomCustomization);

    /* Inicializar ZoomSDK () */
    console.log(`Se inicializa la librera facezoom con la key:::${environment.zoomDeviceLicenseKey}`);
    setTimeout(() => {
      ZoomSDK.initialize(
        environment.zoomDeviceLicenseKey ?
        environment.zoomDeviceLicenseKey : 'dxWd7SzSTiJ5B0fyjZNyo4jgnfzHtpNM', (initializationSuccessfull) => {
        this.disableButton = !initializationSuccessfull;
      });
    }, 500);
  }

  /* Se manda llamar el servicio de prueba de vida para la captura del rostro. */
  startLivenessCheck() {
    this.zoomManagedSession = new ZoomSDK.ZoomManagedSession(
      (response) => this.onZoomManagedSessionComplete(response),
      this.deviceLicenseKeyIdentifier,
      this.zoomServerBaseURL,
      ZoomSDK.ZoomManagedSessionMode.Liveness
    );
  }

  /*
    Se ejecuta una vez que la captura de rostro sea satisfactoria,
    devuelve un status y en el objeto 'zoomManagedSession' se encuentran
    los datos capturados del rostro.
  */
  onZoomManagedSessionComplete(zoomManagedSessionStatus: any) {
    // tslint:disable-next-line: no-unused-expression
    ZoomSDK.ZoomManagedSessionStatus[zoomManagedSessionStatus];
    if (this.zoomManagedSession) {
      const results = this.zoomManagedSession.getLatestZoomSessionResult();
      const faceMetrics = results.faceMetrics;
      this.sessionId = results.sessionId;
      this.face = faceMetrics.auditTrail.length > 0 ? faceMetrics.auditTrail[0] : this.face;
      // Persistimos el preview de la imgen
      this.faceExt = 'JPEG';
      this.obser.sendData(this.face, FACE_IMAGE);
      // Capturamos la imgen tridimensional
      faceMetrics.getFaceMapBase64((faceMap: string) => {
        if (faceMap) {
          this.saveFace({ face: this.face, faceMap });
        }
      });
    } else {
      console.error('ZoOm initialization unsuccessful.');
    }
  }

  saveFace(images: { [key: string]: any }) {
    this.disableButton = true;
    const SUBSCRIPTION = this.saveImages({ images }, false).subscribe((response) => {
      if (response.data && response.data.status ) {
        const ref = response.data.imagesRef;
        this.onCall(this.getDataToPayload({ face: ref.img_face, faceMap: ref.img_faceMap }));
        setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
      }
    },
    error => {
      this.disableButton = false;
    });
  }

  getIntentosMaximos() {
    const SUBSCRIPTION = this.catalogo.data('BIO001', this.params([PAIS, MODULO, LENGUAJE])).subscribe(
      response => {
        this.intentosMaximos = response && (response.length > 0)
        ? response[0].maximosReintentos.maximosReintentosSelfie : 3;
        setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
      }
    );
  }

  getImageExtension(imageBase64: string) {
    return imageBase64.match(/[^:/]\w+(?=;|,)/)[0].toUpperCase();
  }

  showModal(mensaje) {
    const modal: Modal = {
      display: true,
      buttons: [
        {
          callback: ( close ) => {
            close();
          },
          buttonText: 'Volver a tomar foto',
          class: 'alertModal__footer--button alertModal__footer--button--primary'
        }
      ],
      title: '',
      message: mensaje,
      entryComponent: AlertModalTMAComponent
    };
    this.obser.sendData(modal, MODAL);
  }

  // Consulta de catalogo la configuracin de la calidad de imagenes
  getConfig() {
    const SUBSCRIPTION = this.catalogo.data('BIOCONFIG', this.params([PAIS])).subscribe(
      response => {
        this.config = response && (response.length > 0)
        ? response[0] : {
          showLog: '0',
          limit: 900000,
          app: {
            quality: 0.7,
            width: 2400
          },
          mobile: {
            quality: 0.7,
            width: 2400
          },
          web: {
            quality: 0.8,
            width: 2400
          }
        };
        setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
      }
    );
  }

  compress(file: any,  max: number, quality: number = 0.8): Observable<any> {
    const width = max;
    const reader = new Image();
    const mimeType = this.faceExt;
    reader.src = file;
    return new Observable(observer => {
      reader.onload = ev => {
        const img = new Image();
        img.src = (ev.target as any).src;
        img.onload = () => {
          const elem = document.createElement('canvas');
          const scaleFactor = width / img.width;
          elem.width = width;
          elem.height = img.height * scaleFactor;
          const ctx = (elem.getContext('2d') as CanvasRenderingContext2D);
          ctx.drawImage(img, 0, 0, width, img.height * scaleFactor);
          const newBase64 = ctx.canvas.toDataURL(mimeType, quality);
          observer.next(newBase64);
        };
        reader.onerror = error => observer.error(error);
      };
    });
  }

}
