import { Component, OnInit, OnDestroy } from '@angular/core';
import { BaseComponent } from 'src/app/modules/common/components/base/base.component';
import { Payload, BackButton } from 'src/app/modules/common/components/base/base';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { PAIS, MODULO, LENGUAJE } from 'src/app/modules/common/CONST';
import { ViewEncapsulation } from '../../../common/components/encaptulation';
@Component({
  selector: 'app-bio005',
  templateUrl: './bio005.component.html',
  styleUrls: ['./bio005.component.scss', '../../../../../assets/cam/scss/components/biometria/_bio.scss'],
  encapsulation: ViewEncapsulation.ShadowDom
})
export class Bio005Component extends BaseComponent implements OnInit, OnDestroy, Payload, BackButton {
  questions: Array<string>;
  answer: Array<string>;
  ipAddress: string;
  intentosMaximos: any;

  ngOnInit() {
    this.getIntentosMaximos();
    this.payload.preguntas = this.payload.preguntas.map((valor) => {
      const pre = valor.pregunta.respuestas.map((item) => {
        item.value = item.idRespuesta;
        item.label = item.textoRespuesta;
        return item;
      });
      valor.pregunta.respuestas = pre;
      return valor;
    });
 //   console.log(this.payload.preguntas);
    this.obtenerPreguntas();
    this.setRules({
      preguntas: this.formBuilder.array(this.generarPreguntas())
    });
  }

  ngOnDestroy(): void {
    //  throw new Error('Method not implemented.');
  }

  getDataToPayload(data?: any): () => { [key: string]: any; } {
    const form = this.formulario.getRawValue();
    form.preguntas.map(obj =>
      obj.textoRespuesta = this.getTextoRespuesta(obj.idPregunta, obj.idRespuesta)
    );
    return () => ({
      pais: this.pais,
      maximosReintentosReto: this.intentosMaximos,
      respuestas: form,
    });
  }

  getDataToBackButton(data?: any): () => { [key: string]: any; } {
    return () => ({
    });
  }

  obtenerPreguntas() {
    this.questions = this.payload.preguntas;
  }

  getIntentosMaximos() {
    const SUBSCRIPTION = this.catalogo.data('BIO001', this.params([PAIS, MODULO, LENGUAJE])).subscribe(
      response => {
        this.intentosMaximos = response && (response.length > 0)
          ? response[0].maximosReintentos.maximosReintentosReto : 1;
        setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
      }
    );
  }

  generarPreguntas(): Array<FormGroup> {
    return this.payload.preguntas.map((valor, i) => {
      console.log(valor);
      return this.formBuilder.group({
        idPregunta: [this.payload.preguntas[i].pregunta.idPregunta, []],
        idRespuesta: ['', [
          this.rules.validationForSelect(() => this.payload.preguntas[i].pregunta.respuestas, () => false)
        ]],
        textoRespuesta: ['', []]
      });
    });
  }

  getTextoRespuesta(idPregunta: string, idRespuesta: string): string {
    const res = this.payload.preguntas.filter(p => p.pregunta.idPregunta === idPregunta).length > 0
    ? this.payload.preguntas.filter(p => p.pregunta.idPregunta === idPregunta)[0].pregunta.respuestas : '';
    return res === '' ? ''
    : res.filter(r => r.idRespuesta === idRespuesta).length > 0
    ? res.filter(r => r.idRespuesta === idRespuesta)[0].textoRespuesta
    : '';
  }
}
