import { Component, OnInit, OnDestroy, ViewChild, ElementRef } from '@angular/core';
import { BaseComponent } from 'src/app/modules/common/components/base/base.component';
import { Payload, BackButton } from 'src/app/modules/common/components/base/base';
import { ViewEncapsulation } from 'src/app/modules/common/components/encaptulation';
import { WEBCAM_ERRORS, MODAL, LOGGER, PAIS, IMAGE_FRONT, LOADING, STEP_ID } from 'src/app/modules/common/CONST';
import { ICaptureFrontDocument, IBioConfig } from '../../common/models/biometria.model';
import { WebcamImage, WebcamInitError, WebcamUtil } from 'ngx-webcam';
import { Subject, Observable } from 'rxjs';
import { SafeResourceUrl } from '@angular/platform-browser';
import { Modal } from 'src/app/modules/common/components/modal/modal';
import { AlertModalTMAComponent } from 'src/app/modules/common/components/entryComponents/alert-modal-tma/alert-modal-tma.component';

@Component({
  selector: 'app-bio001',
  templateUrl: './bio001.component.html',
  styleUrls: ['./bio001.component.scss', '../../../../../assets/cam/scss/components/biometria/_bio.scss'],
  encapsulation: ViewEncapsulation.ShadowDom
})
export class Bio001Component extends BaseComponent implements OnInit, OnDestroy, Payload, BackButton {
  frontDni: string;
  frontDniExt: string;
  uploadDNIRequest: { [key: string]: any };
  trigger: Subject<void>;
  webcamErrors: Array<string>;
  pathId: string;
  widthImage: number;
  heightImage: number;
  imagePreview: string;
  captureFlag: boolean;
  quality: number;

  public urlSafe: any;
  private config: IBioConfig;
  ngOnInit(): void {
    this.quality = 1;
    this.webcamErrors = [];
    this.trigger = new Subject<void>();
    this.urlSafe = require('../../../../../assets/cam/img/bio/cedula_1.svg');
    this.uploadDNIRequest = {};
    this.imagePreview = '';
    this.captureFlag = true;
    // Determina el ancho y el alto de la imgen a capturar
    if (!this.isDesktopDevice) {
      this.widthImage = 360;
      this.heightImage = 360;
    } else {
      this.widthImage = 960;
      this.heightImage = 480;
    }
    this.getConfig();

    this.obser.getData(MODAL).subscribe((response) => {
      if (response ) {
        this.frontDni = null;
      }
    });

    /* Verifca si esta embebido en APK y llama postMessage registerPath. */
    if (this.canal === '2') {
      this.pathId = this.payload.identificadorPaso;
      this.serverEnrollmentKey = this.payload.llaveEnrolamiento;
      if (this.payload.tipoEnrolamiento) {
        const registerPathReponse = {
          code: this.payload.codigoRespuesta,
          description: this.payload.mensajeRespuesta,
          serverEnrollmentKey: this.payload.llaveEnrolamiento,
          uuidDevice: this.payload.uuidDispositivo,
          uuidTransaction: this.payload.idProceso,
          typeEnrollment: this.payload.tipoEnrolamiento,
          userId: this.payload.numeroDocumentoUsuario,
          userType: this.payload.tipoDocumentoUsuario
        };
        this.registerPath(registerPathReponse).subscribe(() => this.disableButton = false);
      } else {
        this.disableButton = false;
      }
    }
  }

  ngOnDestroy(): void {
    // throw new Error('Method not implemented.');
  }

  getDataToPayload(data?: ICaptureFrontDocument): () =>  ({ [key: string]: any; }) {
    return () => ({
      imagenFrenteIdentificacion: data.frontDni || '',
      formatoImagenDocumento: this.frontDniExt || ''
      // uploadDNIRequest: data.uploadDNIRequest || {}
    });
  }

  getDataToBackButton(data?: any): () => ({ [key: string]: any; }) {
    return () => ({});
  }

  /*
    Se llama post message 'captureFrontDocument' que activa la cmara
    del telfono para tomar la foto de la parte frontal del documento.
  */
  mobileCaptureFrontDocument(): void {
    const SUBSCRIPTION = this.captureFrontDocument(this.pathId).subscribe((response: ICaptureFrontDocument) => {
      if (response && response.status) {
        this.disableButton = true;
        if (response.status === '01') {
          this.obser.sendData(false, LOADING);
          this.showModal(`Ha ocurrido un error, por favor vuelva a intentarlo`);
          this.disableButton = false;
          setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
          return false;
        }
        if (response.status === '02') {
          this.obser.sendData(false, LOADING);
          this.disableButton = false;
          setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
          return false;
        }
        if (response.status === '00') {
          this.obser.sendData({
            mostrar: true,
            footer: '<strong>Estamos procesando su solicitud</strong>',
            header: '<strong>Espere un momento por favor</strong>'
          }, LOADING);
          const obj: any = {};
          let cont = 0;
          const imgStrZip = this.config.compress === 'ACTIVE' ? this.zipString.compress(response.frontDni) : response.frontDni;
          const limite = +this.config.limit;
          for (let j = 0; j < imgStrZip.length; j = j + limite) {
            obj[`frontDni_${cont}`] = imgStrZip.substr(j, limite);
            cont ++;
          }
          this.saveFrontDocument(obj, false);
        }
        setTimeout(() => SUBSCRIPTION.unsubscribe(), 500);
      }
    });
  }

  /*
    Manda activar observable que captura la imagen de la camara web.
  */
  triggerCaptureFrontDocument(): void {
    if (this.webcamErrors.length <= 0) {
      this.trigger.next();
    }
  }

  /*
    Evento que se activa cuando se captura la imagen de la camara web.
  */
  handleCaptureFrontDocument(frontDni: WebcamImage): void {
    if (frontDni && this.captureFlag) {
      this.captureFlag = false;
      this.disableButton = true;
      this.imagePreview = frontDni.imageAsDataUrl.toString();
      this.obser.sendData(this.imagePreview, IMAGE_FRONT);
      if (frontDni.imageAsDataUrl.toString().length > this.config.limit) {
        const SUBSCRIPTION = this.compress(frontDni.imageAsDataUrl, this.config.web.width, this.config.web.quality)
        .subscribe(frontWebDni => {
          // this.urlSafe = this.sanitizer.bypassSecurityTrustUrl(frontWebDni);
          if (frontWebDni.length > this.config.limit) {
            const SUBSCRIPTION2 = this.compress(frontDni.imageAsDataUrl, this.config.web.width * 0.8, this.config.web.quality - 0.1)
              .subscribe(frontWebDni2 => {
                this.frontDniExt = this.getImageExtension(frontWebDni2);
                this.saveFrontDocument({ frontDni: frontWebDni2 }, true);
                setTimeout(() => SUBSCRIPTION2.unsubscribe(), 300);
            }, err => {
              this.disableButton = false;
              this.captureFlag = true;
            });
          } else {
            this.frontDniExt = this.getImageExtension(frontWebDni);
            this.saveFrontDocument({ frontDni: frontWebDni }, true);
          }
          setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
        },
        error => {
          this.disableButton = false;
          this.captureFlag = true;
        });
      } else {
        this.frontDniExt = this.getImageExtension(this.imagePreview);
        this.saveFrontDocument({ frontDni: this.imagePreview }, true);
      }
    }
  }

  get triggerCaptureFrontDocumentObservable(): Observable<void> {
    return this.trigger.asObservable();
  }

  // Nueva funcin que captura la imgen desde web mobile
  capture(event: any) {
    this.disableButton = true;
    const reader = new FileReader();
    reader.readAsDataURL( event.target.files[0]);
    reader.onloadend = (ev: any) => {
      this.disableButton = true;
      this.frontDni = reader.result.toString();
      this.obser.sendData(this.frontDni, IMAGE_FRONT);
      this.imagePreview = this.frontDni;
      this.frontDniExt = this.getImageExtension(this.frontDni);
      this.urlSafe = this.sanitizer.bypassSecurityTrustUrl(this.frontDni);

      // Si supera el limite configurado la comprime
      if (this.frontDni.length > this.config.limit) {
        const SUBSCRIPTION = this.compress(reader.result, this.config.mobile.width, this.config.mobile.quality).subscribe(frontDni => {
          if (frontDni.length > this.config.mobile.width) {
            const SUBSCRIPTION2 = this.compress(reader.result, this.config.mobile.width * 0.9, this.config.mobile.quality - 0.1)
            .subscribe(frontDni2 => {
              if (frontDni2.length > this.config.limit) {
                const SUBSCRIPTION3 = this.compress(reader.result, this.config.mobile.width * 0.9, this.config.mobile.quality - 0.1)
                .subscribe(frontDni3 => {
                  this.saveFrontDocument({ frontDni: frontDni3 }, true);
                  setTimeout(() => SUBSCRIPTION3.unsubscribe(), 300);
                },
                error => {
                  this.disableButton = false;
                });
              } else {
                this.saveFrontDocument({ frontDni: frontDni2 }, true);
              }
              setTimeout(() => SUBSCRIPTION2.unsubscribe(), 300);
            },
            error => {
              this.disableButton = false;
            });
          } else {
            this.saveFrontDocument({ frontDni }, true);
          }
          setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
        },
        error => {
          this.disableButton = false;
        });
      } else {
        this.frontDniExt = this.getImageExtension(reader.result.toString());
        this.urlSafe = this.sanitizer.bypassSecurityTrustUrl(reader.result.toString());
        this.saveFrontDocument({ frontDni: this.frontDni }, true);
      }
    };
  }

  handleChangeImage(e: any) {
    console.log(e);
  }

  handleInitError(error: WebcamInitError): void {
    if (error.mediaStreamError && WEBCAM_ERRORS.hasOwnProperty(error.mediaStreamError.name)) {
      // this.disableButton = true;
      this.webcamErrors.push(WEBCAM_ERRORS[error.mediaStreamError.name]);
    }
  }

  saveFrontDocument(images: { [key: string]: any }, encrip: boolean) {
    this.disableButton = true;
    const SUBSCRIPTION = this.saveImages({ images }, encrip).subscribe((response) => {
      if (response.data && response.data.status ) {
        const ref = response.data.imagesRef;
        let imgStr = '';
        Object.keys(ref).map((data, index) => {
          imgStr = index === 0 ? ref[data] : `${imgStr}|${ref[data]}`;
        });
        // Si esta en la web, llama a WF y continua el flujo, de lo contrario persiste la referencia
        if (this.canal === '1') {
          this.onCall(this.getDataToPayload({ frontDni: imgStr, uploadDNIRequest: this.uploadDNIRequest }));
        } else {
          this.obser.sendData(imgStr, IMAGE_FRONT);
          console.log(`imageFront::${this.imageFront}`);
          this.obser.sendData(false, LOADING);
          this.obser.sendData('BIO002', STEP_ID);
          this.route.navigate(['cam/biometria/bio002']);
        }
        this.captureFlag = true;
        setTimeout(() => SUBSCRIPTION.unsubscribe(), 500);
      }
    },
    error => {
      this.disableButton = false;
      this.imagePreview = '';
      this.captureFlag = true;
      this.obser.sendData(false, LOADING);
      this.showModal(`Ha ocurrido un error, por favor vuelva a intentarlo`);
    });
  }

  getImageExtension(imageBase64: string) {
    return imageBase64.match(/[^:/]\w+(?=;|,)/)[0].toUpperCase();
  }

  getImageMimeType(imageBase64: string) {
    return imageBase64.match(/data:([a-zA-Z0-9]+\/[a-zA-Z0-9-.+]+).*,.*/)[1];
  }

  compress(file: any, max: number, quality: number = 0.8): Observable<any> {
    const width = max;
    const reader = new Image();
    const mimeType = this.getImageMimeType(file);
    reader.src = file;
    return new Observable(observer => {
      reader.onload = ev => {
        const img = new Image();
        img.src = (ev.target as any).src;
        img.onload = () => {
          const elem = document.createElement('canvas');
          const scaleFactor = width / img.width;
          elem.width = width;
          elem.height = img.height * scaleFactor;
          const ctx = (elem.getContext('2d') as CanvasRenderingContext2D);
          ctx.drawImage(img, 0, 0, width, img.height * scaleFactor);
          const newBase64 = ctx.canvas.toDataURL(mimeType, quality);
          observer.next(newBase64);
        };
        reader.onerror = error => observer.error(error);
      };
    });
  }

  showModal(mensaje) {
    const modal: Modal = {
      display: true,
      buttons: [
        {
          callback: ( close ) => {
            close();
          },
          buttonText: 'Volver a tomar foto',
          class: 'alertModal__footer--button alertModal__footer--button--primary'
        }
      ],
      title: '',
      message: mensaje,
      entryComponent: AlertModalTMAComponent
    };
    this.obser.sendData(modal, MODAL);
  }
  // Consulta de catalogo la configuracin de la calidad de imagenes
  getConfig() {
    const SUBSCRIPTION = this.catalogo.data('BIOCONFIG', this.params([PAIS])).subscribe(
      response => {
        console.log('aqui', response);
        this.config = response && (response.length > 0)
        ? response[0] : {
          showLog: '0',
          limit: 900000,
          app: {
            quality: 0.7,
            width: 2400
          },
          mobile: {
            quality: 0.7,
            width: 2400
          },
          web: {
            quality: 0.8,
            width: 2400
          }
        };
        setTimeout(() => SUBSCRIPTION.unsubscribe(), 300);
      }
    );
  }
}
